html {
  scroll-behavior: smooth;
   min-width: 300px; 
   overflow-x: hidden;
}

body {
  margin: 0;
  background-color: white;
  width: 100%;
  height: 100%;
  font-family: Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

.column {
  float: left;
}

.center {
  width: 100%;
  text-align: center;
  margin: auto;
}

.column.left66-1 {
  width: 66%;
}

.column.right33-2 {
  width: 33%;
}

.column.left {
  width: 50%;
}

.column.right {
  width: 50%;
}

.column.left66 {
  width: 66%;
}

.column.right33 {
  width: 33%;
}

.column.left33 {
  width: 33%;
}

.column.center33 {
  width: 33%;
}

.column.left1 {
  width: 25%;
}

.column.left2 {
  width: 25%;
}

.column.right1 {
  width: 25%;
}

.column.right2 {
  width: 25%;
}

.column.right15 {
  width: 15%;
}

.column.right35 {
  width: 35%;
}

.footer {
  background-color: #939598;
  width: 100%;
  margin: 0;
  padding-left: 17%;
  padding-right: 17%;
  padding-top: 2%;
}

.footer div {
  width: auto;
  height: fit-content;
  /* margin-bottom: 15px;
  margin-top: 35px;
  scroll-margin-top: 75px; */
}


.footer-icon {

  object-fit: contain;
  height: auto;
  width: 100%;
  max-width: 250px;
  /* padding-top: 1.375em;
  padding-left: 0.25em;
  padding-right: 0.25em; */
  padding: 0.25em;

}

.footer-icon.maceng {

  object-fit: contain;
  height: auto;
  width: 80%;
}



.logo-stack{
  margin-bottom: 50px;
}

.social-media-icon {
  margin-bottom: 1.3750em;
  margin-top: 0.25em;
  margin-left: auto;
  margin-right: auto;
  max-width: 50px;
  max-height: 50px;
  /* margin-bottom: 50px; */
}

/* .row {
  margin: 25px;
} */

.row:after {
  content: "";
  display: table;
  clear: both;
}

.containers {
  width: 95%;
  margin: 0 auto;
  margin-top: 120px;
}

.containers div {
  margin-bottom: 20px;
  scroll-margin-top: 105px;
}  

.containers.section {
  width: auto;
  height: fit-content;
  margin-top: 35px;
}  

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.subtitle {
  color: #781214;
  /* font-size: 40px; */
  font-size: 2.5em;
  margin: 0;
  padding: 10px;
  font-style: Verdana;
  width: fit-content;
}

.subtitle.center {
  margin-left: auto;
  margin-right: auto;
}

.description {
  color: black;
  font-size: 1.25em;
  /*font-size: 1.8vw;*/
  text-align: left;
  margin: 0;
  padding: 10px;
  font-style: Verdana;
  width: fit-content;
}

.sponsor-group {
  margin-top: 75px;
}

.platnium-sponsor-logo {
  object-fit: contain;
  width: 100%;
  height: 250px;
}

.gold-sponsor-logo {
  object-fit: contain;
  width: 100%;
  height: 200px;
}

.silver-sponsor-logo {
  object-fit: contain;
  width: 100%;
  height: 150px;
}

.bronze-sponsor-logo {
  object-fit: contain;
  width: 100%;
  height: 125px;
  /* padding-left: 25%;
  padding-right: 25%;
  padding-top: 20%;
  padding-bottom: 20%; */

  padding-right: clamp(2px, 15%, 25%);
  padding-left: clamp(2px, 15%, 25%);
  padding-top: clamp(2px, 20%, 20%);
  padding-bottom: clamp(2px, 20%, 20%);
}

.header-logo {
  max-width: 150px;
  max-height: 75px;
}

.header-title {
  max-width: 250px;
  max-height: 150px;
}

.image {
  object-fit: contain;
  width: 100%;
  min-width: 500px;
  margin-top: 25px;
}

.image.team {
  box-shadow: 2px 4px 8px 2px #781214ab, 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-top: 15px;
}

.image.tall {
  object-fit: contain;
  height: 100%;
  margin-top: 25px;
  max-height: 500px;
}

.image.small {
  height: 150px;
}

.icon {
  max-width: 150px;
  max-height: 150px;
}

.medium-icon {
  max-width: 200px;
  max-height: 150px;
}

figure.item {
  vertical-align: top;
  display: inline-block;
  text-align: center;
  width: fit-content;
  margin-bottom: 10px;
}

.caption {
  display: block;
  color: #494B4B;
  /* font-size: 28px; */
  font-size: 1.75em;
  font-style: Verdana;
  margin-top: 15px;
}

.social-media-icon-container{
  text-align: center;
  display: grid;
  margin-left: auto;
  margin-right: auto;
}

