.contact-container {
  text-align: left;
  margin-left: 10px;
  margin-top: 0px;
}

.contact-container h6{
  /* font-size: 24px; */
  font-size: 1.5em;
}

.contact-container ul{
  padding: 0;
  list-style-type: none;
}

.contact-container address {
  color: black;
  font-size: 20px;
  text-decoration: none;
}

.contact-container a:hover {
  color: #781214;
}


.header {
  overflow: hidden;
  background-color:  #f1f1f1;
  padding: 20px 10px;
  position: fixed;
  top: 0;
  width: 100%;
  height: 105px;
}

/* Style the header links */
.header-left{
  float: left;
}

.header a {
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  /* font-size: 22px; */
  font-size: 1.4em;
  line-height: 25px;
  border-radius: 4px;
}

/* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.header a.logo {
  /* font-size: 25px; */
  font-size: 1.5em;
  font-weight: bold;
}

/* Change the background color on mouse-over */
/* .header a:hover {
  background-color: #e2e0e0;
  color: black;
} */

/* Style the active/current link*/
.header a.active {
  background-color: #781214;
  color: white;
}

/* Float the link section to the right */
.header-right {
  float: right;
  padding-top: 25px;
}

.shad {
  box-shadow: 1px 2px 4px 1px rgb(27, 27, 27);
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
}

.shad:hover {
  box-shadow: 3px 6px 12px 3px #781214ab, 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

 .shadow {
  
  background-color: white;
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 5px;
 }

.shadow::after {
  border-radius: 5px;
  width: 100%;
  height: 100%;
} 


 .shadow:hover {
  box-shadow: 2px 4px 8px 2px #781214ab, 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}


